body {
	background-color: #F7F7F7;
	padding: 0;
	margin: 0;
}

.title {
	text-align: center;
}
#help{
	margin: auto;
	display:block;
}
#close {
	margin-left:95%;
}
#container {
	.contain {
		display:flex;	
		height: 100vh;
#regles{
	padding: 20px;
	width:20%;
	height: auto;
	color: black;
	display: inline-block;
	vertical-align:top;
	display: none;
	background-color: rgba(0,0,0,0.1);
}
		.CodeMirror {
			height: 100%;
		}
		.ReactCodeMirror {
			height: 100%;
			width:49%;
			vertical-align: top;
		}
	}
	.resultat {
		width:49%;
		overflow-y: scroll;
		padding-left: 5px;
		color:black;
	}
}
::-webkit-scrollbar {
    width: 12px;
}
 
::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3); 
    border-radius: 10px;
}
 
::-webkit-scrollbar-thumb {
    border-radius: 10px;
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.5); 
}